.rounded-corner {
    border-radius: 15px; 
    padding: 20px; 
    background-color: #f8f9fa; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
}

.rounded-corner h2 {
    text-align: center; 
    color: #333; 
}

.testimonials-container {
    display: flex; 
    overflow-x: auto; 
    scroll-behavior: smooth; 
    -ms-overflow-style: none; 
    scrollbar-width: none; 
    
}
.quote-mark {
    position: absolute; /* Positioning the quote mark */
    bottom: 20px; /* Distance from the bottom */
    right: 20px; /* Distance from the left */
    font-size: 2em; /* Increased size of the quote mark */
    color: rgba(173, 216, 230, 1); /* Light blue color */
    opacity: 0.9; /* Slightly transparent for a softer effect */
    pointer-events: none; /* Prevent interaction */
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}


.testimonials-container::-webkit-scrollbar {
    display: none; 
}

.testimonial-card {
    min-width: 300px; 
    max-width: 700px; 
    margin: 10px; 
    margin-top: 80px; 
    padding: 50px 15px 15px; 
    background-color: #fff; 
    border-radius: 10px; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
    position: relative; 
    transition: transform 0.2s; 
}

.testimonial-card:hover {
    transform: scale(1.05); 
    cursor: pointer; 
}

.reviewer-image {
    width: 100px; 
    height: 100px; 
    border-radius: 50%; 
    position: absolute; 
    left: 10px; 
    top: -50px; 
    border: 3px solid #f8f9fa; 
    object-fit: cover;
    max-width: 100%; /* Make image responsive */
    max-height: 100%; /* Maintain aspect ratio */

}


.scroll-button {
    background-color: rgba(255, 255, 255, 0.8); /* Semi-transparent white background */
    border: none; /* Remove border */
    border-radius: 50%; /* Circular buttons */
    width: 40px; /* Width of the button */
    height: 40px; /* Height of the button */
    align-items: center; /* Center icon vertically */
    justify-content: center; /* Center icon horizontally */
    cursor: pointer; /* Change cursor on hover */
    transition: background-color 0.3s; /* Smooth transition for hover effect */
}

.scroll-button:hover {
    background-color: rgba(255, 255, 255, 1); /* Change background on hover */
}
.reviewer-address{
  font-size: 14px;
  padding: 0;
  margin: 0;
  font-weight:400;
}
.reviewer-Name{
  padding: 0;
  margin: 0;
  font-weight: bolder;
}

/* Testimonials.css */

/* General star styles */
.star {
    font-size: 24px; /* Size of the star */
    color: lightgray; /* Default color for unfilled stars */
    margin: 0 2px; /* Spacing between stars */
    transition: color 0.3s; /* Smooth transition for hover effects */
}

/* Filled star styles */
.star.filled {
    color: gold; /* Color for filled stars */
}

/* Optional hover effect */
.star:hover {
    color: orange; /* Change color on hover */
}
