.centered-section {
  padding: 4rem 1rem 4rem 1rem; /* Reasonable default padding for smaller screens */
}

@media (max-width: 768px) {
  .centered-section {
    padding: 4rem 3rem;/* Adjust padding for medium and larger screens */
  }
}

@media (max-width: 992px) {
  .centered-section {
    padding: 5rem 3rem; /* More generous padding for larger screens */

  }
}

.right-about {
  padding-left: 4rem; /* Adds gap between image and text on larger screens */
}

@media (max-width: 768px) {
  .right-about {
    padding-left: 0; /* Removes padding on smaller screens */
  }
}

.imageabout {
  width: 100%; /* Make the image responsive */
  max-width:35rem; /* Set a maximum width for larger screens */
  height: auto; /* Ensure the height adjusts automatically */
}

.img-box-shadow {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow for the image */
}

.aboutdiv p ,.aboutdiv b{
    font-size: 1rem;
}
h2{
font-weight: bolder;
font-size: 2rem;
}