.services-container {
  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding: 10px 0;
  white-space: nowrap;
  margin-bottom: 2rem;
}

.services-container::-webkit-scrollbar {
  display: none;
}

.service-card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin: 0 10px;
  padding: 20px;
  text-align: left;
  flex: 0 0 auto;
  width: 305px;
}

.service-icon {
  width: auto;
  height: 150px;
  margin-bottom: 10px;
}

.service-card h5 {
  font-size: calc(1rem + 0.3vw);
  margin-bottom: 0.5rem;
  white-space: normal;
}

.service-card p {
  font-size: 0.95rem;
  color: #555;
  line-height: 1.5;
  text-align: justify;
  word-wrap: break-word;
  white-space: normal; /* Allow wrapping */
  margin-top: 1rem;
}

.navigation-buttons {
  display: flex;
  justify-content: flex-start;
  gap: 10px;
}

.prev-btn,
.next-btn {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.prev-btn:hover,
.next-btn:hover {
  background-color: #0056b3;
}
