/* Scrollable Room Container */
.room-container {
  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
  white-space: nowrap;
}

.room-container::-webkit-scrollbar {
  display: none; /* Hide scrollbar */
}

.room-card {
  flex: 0 0 auto; /* Allow horizontal scrolling */
  width: 305px; /* Adjusted for card width */
  margin: 0 10px; /* Spacing between cards */
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.room-card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Room Image Styles */
.room-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

/* Room Details */
.room-details {
  padding: 1rem;
}

.room-title {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.8rem;
  color: #333;
}

.room-price {
  font-size: 1rem;
  font-weight: 500;
  color: #ff6347;
  margin-bottom: 0.8rem;
}

.room-description {
  font-size: 0.9rem;
  color: #555;
  margin-bottom: 0.8rem;
  text-align: left;
}

.room-services {
  list-style-type: none;
  padding: 0;
  color: #777;
}

.room-services li {
  margin-bottom: 0.4rem;
  font-size: 1rem;
}

.room-services li::before {
  content: '✔️ ';
  color: #28a745;
}

/* Navigation Button Styles */
.room-prev, .room-next {
  background-color: rgb(162, 204, 204);
  z-index: 1000;
  border: none;
  padding: 0.5rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.room-prev {
  left: 0.7rem;
}

.room-next {
  right: 0.7rem;
}
